.header-container {
    margin: 1.5rem 5rem;
}
.header-container h1 {
    text-align: center;
    margin-bottom: 2rem;
}
.event-venue {
    display: flex;
    align-items: center;
    justify-content: center;
}
.school-img img {
    height: 30vh;
    width: 18vw;
    border-radius: 5%;
    margin-right: 2rem;
}
.event-venue table {
    padding: 2rem 15rem 2rem 5rem;
    border: 2px solid rgba(128, 128, 128, 0.193);
    border-radius: 2%;
}
.event-venue table tr td {
    padding: 0.7rem 5rem 0.7rem 0;
    font-size: 1.3rem;
    font-weight: 700;
    border-bottom: 2px solid rgba(128, 128, 128, 0.193);
}